.popup_opened {
    position: fixed;
    display: flex;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, .5);
    justify-content: center;
    align-items: center;
    z-index: 2;
    opacity: 1;
    visibility: visible;
    transition:  visibility 1s, opacity 1s;
}